.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}

.contact button{
    display: block;
    border: 2px solid black;
    background-color: white;
    padding: 5px 10px;
    font-weight: bold;
    cursor: pointer;
    font-size: 20px;
    margin: 0 20% 0 auto;
    transition: .2s;
}


.contact button:hover {
    background-color: black;
    color: white;
}